.sectionQuestion {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card,
.infoLifes {
  width: 100%;
  height: 95vh;
  background-color: rgb(97, 89, 198);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1.5rem;
  justify-content: space-around;

  box-shadow: 0 5px 16px 0 rgb(0 0 0 / 58%);
}
.card {
  transition: all 0.8s ease;
  position: relative;
}

.card h3,
.infoLifes h3 {
  font-family: "Ubuntu", sans-serif;
  font-size: 5vmin;
  color: white;
  text-align: center;
}
.containerLogo {
  display: flex;
  height: 30%;
  flex-direction: column;
  justify-content: space-between;
  /*  gap:2.5rem; */
  align-items: center;
}
.containerLogo h2 {
  font-family: "Ubuntu", sans-serif;
  font-size: 6.5vmin;
  color: white;
  text-align: center;
  margin: 1rem 0;
}
.containerButtons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.buttonOption,
.buttonOptionBombed {
  padding: 1rem 2rem;
  border: none;
  font-family: "Ubuntu", sans-serif;
  font-size: 3vmin;
  background-color: #ceb036;
  border-radius: 10px;
  color: white;
  width: 40%;
  height: 10vh;
  cursor: pointer;
  border-bottom: 5px solid #917a20;
  transition: all 0.5s;
}
.correct {
  background-color: #43b14b;
  border-bottom: 5px solid #3d8842;
}
.incorrect {
  background-color: #cb5b3b;
  border-bottom: 5px solid #943b23;
}
.buttonOptionBombed {
  background-color: #444;
  border-bottom: 5px solid rgb(51, 51, 51);
}
.logo {
  width: 10vw;
}
.buttonOption:hover {
  box-shadow: 0 5px 16px 0 rgb(0 0 0 / 58%);
}
.infoLifes {
  align-items: center;
  justify-content: center;
}
.heart {
  width: 10vw;
}
.powersButtons {
  display: flex;
  gap: 3rem;
}
.powersButtons * {
  border: none;
  background-color: transparent;
  text-align: center;
  display: flex;
  align-self: center;
  gap: 0.5rem;
  width: auto;
}
.imgPowers {
  width: 3vw;
}
.containerIconsPowers {
  display: flex;
  flex-direction: column;
}
.imgPowersCoin {
  width: 1.5vw;
}
.containerCoins {
  display: flex;
}
.containerHeaderCard {
  display: flex;
  width: 100%;
  justify-content: space-between;
  /* gap:2rem; */
  align-items: center;
  /*  border: 1px solid black */
}
.containerInfoGame {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  height: 100%;
}
.imgInfoGame {
  width: 2.5vw;
}
.containerInfoGame span {
  font-family: "Ubuntu", sans-serif;
  color: white;
  font-size: 3vmin;
}
.containerSeconds {
  background-color: #ceb036;
  border-radius: 100%;
  padding: 0.5rem;
}
.seconds {
  font-size: 6vmin;
  color: white;
}

.coinInfo{
  display: flex;
  justify-content: center;
  align-items: center;
}

.coinInfo img{
  margin-right: 5px;
}

.coinInfo span{
  font-size: 2em;
  font-weight: bold;
  color: white;
}

@media screen and (max-width: 600px) {
  .logo {
    width: 25vw;
  }
  .containerButtons {
    margin-top: 5rem;
  }
}
@media screen and (max-width: 540px) {
  .powersButtons {
    gap: 0.9rem;
  }
  /*   hola */
}
@media screen and (max-width: 400px) {
  .powersButtons {
    gap: 0;
  }
  .powersButtons * {
    width: 40%;
  }
}
